<template>
	<center>
	<div class="login-box">
		<img src="@/assets/picture/back-ground/title.png" mode='aspectFit' class="login-title"></image>
		<div class="login-form">
			<p><input class="login-input" placeholder-class placeholder="请输入手机号" /></p>
			<p><input class="login-input" placeholder-class password placeholder="请输入密码"/></p>
			<el-checkbox v-model="checked" class="login-checkbox">记住密码</el-checkbox>
			<el-button @click="handleRouter" type="primary" icon="el-icon-right" circle class="login-next"></el-button>
		</div>
	</div>
	</center>
</template>

<script>
	export default {
	        data () {
	            return {
	                checked: false
	            }
	        },
			methods:{
				handleRouter(){
					this.$router.push('/center');
				}
			}
	}
</script>
<style>
body{
	position: relative;
	background-image: url(https://i.niupic.com/images/2019/11/06/_18.jpeg);
	background-repeat:no-repeat;
	background-size: cover;
	background-position-y: 100px;
}
.login-title{
	position: relative;
	top: 20px;
	left:-10px;
	transform:scale(0.6)
}
.login-input{
	position: relative;
	top: -10px;
	height: 50px;
	width: 250px;
	background-color: rgba(245, 243, 243, 1);
	border-radius: 100px;
	line-height: 20px;
	text-align: center;
	margin-top: 20px;
	border:none;
}
.login-checkbox{
	position: relative;
	top:12px;
	left:12px;
}
.login-next{
	position: relative;
	top:62px;
	left:-42px;
}
</style>



